.contact {
    margin: 0;
    padding: 0;
    &__item {
        
        list-style: none;
        &:nth-child(1) {
            & a {
                &::before {
                    content: '';
                    background: url('../img/phon.svg');
                    width: 23px;
                    height: 23px;
                    display: flex;
                    margin-right: 15px;
                }
            }
        }
        &:nth-child(2) {
            & a {
                &::before {
                    content: '';
                    background: url('../img/mail.svg');
                    width: 22px;
                    height: 18px;
                    display: flex;
                    margin-right: 15px;
                }
            }
        }
        &:nth-child(3) {
            & a {
                &::before {
                    content: '';
                    background: url('../img/location.svg');
                    width: 20px;
                    height: 24px;
                    display: flex;
                    margin-right: 15px;
                }
            }
        }
    }
}