.newsletter {
    margin-left: 21px;
    align-items: flex-start;
    &__textarea {
        width: 400px;
        height: 60px;
        background: $grey;
        border: 1px solid $milk;
        resize: none;
        cursor: pointer;
        margin-top: 14px;
        margin-bottom: 15px;
        &:hover {
            border: 2px solid $orange;
            transition-duration: 0.4s;
            transition-property: border-color;
        }
        &:focus {
            outline: none;
            background: $white;
            border: 2px solid $orange;
            transition-duration: 0.4s;
            transition-property: background-color border-color ;
        }
    }

}