.destination {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 259px;
    &::before {
        content: '';
        width: 172px;
        height: 172px;
        border-radius: 50%;
        background: $green2;
        filter: blur(100px);
        position: absolute;
        top: -230px;
        right: 54px;
    }
    &__list {
        display: flex;
        justify-content: center;
        margin-top: 100px;
        height: 750px;
        align-items: flex-end;
    }
    &__item {
        position: relative;
        & img {
            &:hover{
                transform: scale(1.05);
                transition-duration: 0.6s;
                transition-timing-function: linear;
            }
        }
        &-up {
            position: relative;
            align-self: flex-start;
            &::before {
                content: '';
                position: absolute;
                background: url('../img/Vector.svg');
                width: 169px;
                height: 169px;
                top: 0;
                left: -44%;
            }
            &::after {
                content: '';
                position: absolute;
                background: url('../img/Vector2.svg');
                width: 148px;
                height: 169px;
                top: 0;
                right: -40%;
            }
        }
        &:not(:last-child) {
            margin-right: 30px;
        }
    }
    &__place {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        bottom: -9%;
        left: 12%;
        width: 400px;
        height: 105px;
        background: $white;
        box-shadow: 0px 10px 25px rgba(215, 190, 190, 0.2);
        padding: 20px 32px;
        & h3 {
            margin: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 33px;
            color: $black2;
        }
    }
    &__reviews {
        display: flex;
        align-items: center;
        margin-top: 17px;
        & p {
            margin: 0;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: $orange;
            margin-right: 30px;
        }
        & span {
            margin-left: 7px;
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 15px;
            line-height: 22px;
            color: $wheat;
        }
    }
    & a {
        transform: translateY(-11px);
    }
}