.travel-form {
    width: 71.8%;
    background: $white;
    box-shadow: 0px 10px 30px 5px rgba(219, 200, 197, 0.2);
    display: flex;
    align-items: center;
    position: absolute;
    bottom: -77px;
    right: 0px;
    padding: 45px;
    &__filter{
        margin: 0;
        padding: 0;
        display: flex;
        border: none;
        width: 81.6%;
    }
    &__item {
        display: flex;
        flex-direction: column;
        width: 240px;
        margin-right: 60px;
        padding-bottom: 10px;
        border-bottom: 1px solid $milk;
        border-radius: 0.5px;
        & label {
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 13px;
            line-height: 19px;
            color: $wheat;
        }
        &-arrow {
            background: url('../img/down_arrow.svg');
        }
        &-calender {
            background: url('../img/calender.svg');
            &::-webkit-calendar-picker-indicator {
                color: transparent;
                background: none;
                z-index: 1;
            }
        }
        &-dollar {
            background: url('../img/dollar.svg');
        }
        & select,
        & input {
            border: none;
            background-color: transparent;
            cursor: pointer;
            font-family: "Noto Serif", "Bookman Old Style", serif;
	        font-weight: 400;
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            color: $orange;
            border: 2px solid transparent;
            background-repeat: no-repeat;
            background-position: 100%;
            appearance: none;
            &:focus:not(.focus-visible) {
                outline: none;
            }
        }
    }
}
.focus-visible {
    outline-color: $orange;
}

