.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 130px;
    &__list {
        display: flex;
        padding-bottom: 90px;
        border-bottom: 1px solid $milk;
    }
    &__item {
        display: flex;
        flex-direction: column;
        & p {
            max-width: 450px;
            margin-right: 47px;
        }
        & h3 {
            font-weight: bold;
            font-size: 28px;
            line-height: 26px;
            color: $black2;
            margin: 0 0 30px 0;
        }
        & a {
            display: inline-flex;
            align-items: center;
            &:hover {
                color: $orange;
                &::before {
                    background-color: $orange;
                }
            }
        }
    }
    & p {
        padding-top: 30px;
        margin: 0;
    }
    & span {
        color: $orange;
    }
}
