.links {
    margin: 0;
    padding: 0;
    &-margin {
        margin-right: 70px;
    }
    &__item {
        list-style: none;
        & a {
            &::before {
                content: '';
                width: 10px;
                height: 2px;
                background: $wheat;
                display: block;
                margin-right: 10px;
            }
        }
    }
}