.download {
    margin-top: 200px;
    padding: 157px 134px;
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, #A6F2F2 0%, #DDF181 100%);
    position: relative;
    margin-bottom: 50px;
    & > img {
        position: absolute;
        top: 20px;
        left: 0;
    }
    &-phone1 {
        z-index: 1;
    }
    &__add {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        & h2 {
            max-width: 619px;
        }
    }
    &__app {
        display: flex;
        align-items: center;
        margin-top: 60px;
        & a {
            z-index: 4;
            &:nth-child(1){
                margin-right: 27px;
            }
        }
    }
    &__img {
        width: 50%;
        & > img {
            display: flex;
            position: absolute;
            &:nth-child(1) {
                bottom: 6%;
                right: 23%;
                z-index: 4;
            }
            &:nth-child(2) {
                top: 42%;
                right: 21%;
                z-index: 4;
            }
            &:nth-child(3) {
                bottom: 0;
                right: 19.1%;
                z-index: 3;
            }
            &:nth-child(4) {
                top: 8%;
                right: 25%;
            }
        }
        
        
    }
}