.button {
    cursor: pointer;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: $white;
    background: $orange;
    &:hover {
        background: $orange2;
        transition-duration: 0.4s;
    }
    &-travel {
        padding: 18px 36.5px;
    }
    &-form {
        padding: 20px 37px;
    }
    &-top-destinations {
        padding: 20px 34px;
    }
    &-plus {
        border: none;
        font-weight: bold;
        font-family: "Lexend Deca", "Tahoma", sans-serif;
        font-size: 24px;
        line-height: 29px;
        padding: 10px 18px;
    }
    &-destinations {
        padding: 20px 41.85px;
    }
    &-footer {
        padding: 20px 31px;
        border: none;
    }
}